(function() {
  angular.module('tskAng', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap']);

}).call(this);



(function() {
  angular.module('tskAng').controller('itemsCtrl', function($scope, Item) {
    $scope.items = Item.index();
    $scope.currentPage = 1;
    $scope.itemsPerPage = 10;
    $scope.maxSize = 5;
    $scope.setPage = function(pageN) {
      $scope.currentPage = pageN;
    };
  }).factory('Item', function($resource) {
    return $resource('http://localhost:3000/api/items/:id.json', {
      id: '@id'
    }, {
      index: {
        method: 'GET',
        isArray: true
      },
      show: {
        method: 'GET',
        isArray: false
      }
    });
  }).controller('itemCtrl', function($scope, Item, $stateParams) {
    $scope.item = Item.show($stateParams);
  });

}).call(this);



(function() {
  angular.module('tskAng').controller('categoryCtrl', function($scope, Category) {
    $scope.categories = Category.index();
  }).factory('Category', function($resource) {
    return $resource('http://localhost:3000/api/categories/:id.json', {}, {
      index: {
        method: 'GET',
        isArray: true
      }
    });
  });

}).call(this);



(function() {
  angular.module('tskAng').run(function($log) {
    'ngInject';
    return $log.debug('runBlock end');
  });

}).call(this);



(function() {
  angular.module('tskAng').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $stateProvider.state('index', {
      url: '/',
      views: {
        categories: {
          templateUrl: 'app/templates/category.html',
          controller: 'categoryCtrl',
          controllerAs: 'category'
        },
        items: {
          templateUrl: 'app/templates/items.html',
          controller: 'itemsCtrl',
          controllerAs: 'items'
        }
      }
    }).state('item', {
      url: '/item/:id',
      templateUrl: 'app/templates/item.html',
      controller: 'itemCtrl',
      controllerAs: 'item'
    });
    return $urlRouterProvider.otherwise('/');
  });

}).call(this);



(function() {
  angular.module('tskAng');

}).call(this);



(function() {
  angular.module('tskAng').config(function($logProvider) {
    'ngInject';
    return $logProvider.debugEnabled(true);
  });

}).call(this);



angular.module("tskAng").run(["$templateCache", function($templateCache) {$templateCache.put("app/templates/category.html","<div ui-view class=\"col-sm-3 sidenav\"><h4>Categories</h4><ul class=\"nav nav-pills nav-stacked\"><div ng-repeat=\"ctg in categories\"><li>{{ ctg.name }}</li></div></ul></div>");
$templateCache.put("app/templates/item.html","<div class=col-sm-9 ng-controller=itemCtrl><div>{{ item.name }}</div><div>{{item.price | currency }}</div><div>{{ item.description }}</div></div>");
$templateCache.put("app/templates/items.html","<div ui-view class=col-sm-9 ng-controller=\"itemsCtrl as items\"><div ng-repeat=\"item in items.slice(((currentPage-1)*itemsPerPage),((currentPage)*itemsPerPage))\"><a ui-sref=\"item({id: item.id})\">{{ item.name }}</a><p></p></div><uib-pagination total-items=items.length ng-model=currentPage class=pagination-sm items-per-page=itemsPerPage></uib-pagination></div>");}]);